import { useRef, useEffect } from "react";
var FIRE_EFFECT_ONLY_ONCE = [];
/*
    dataRef is to call Data methods from outside( Data.scrollTo(), etc. ).
    As it is not dom-related, I decided to avoid forwardRef
*/

var useStore = function useStore(StoreConstructor, dataRef) {
  var finalDataRef = useRef();
  var Store = finalDataRef.current;

  if (!Store) {
    Store = finalDataRef.current = new StoreConstructor();

    if (dataRef) {
      dataRef.current = Store;
    }
  }

  useEffect(function () {
    return function () {
      Store.destructor();
    };
  }, FIRE_EFFECT_ONLY_ONCE);
  return Store;
};

export default useStore;